.message {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}

.avatar {
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

.content {
    padding: 10px;
    border-radius: 5px 5px 5px 0;
    background-color: gray;
    border-top: 1px solid white;
    border-left: 10px solid gray;
    border-bottom: 10px solid white;
    border-right: 50px solid white;
}

