text {
    min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;
}

.name {
    font-size: 20px;
    font-weight: bold;
    color: #61dafb;
}

.time {
    display: flex;
    justify-content: flex-end;
    color: black;
}